@import url(https://fonts.googleapis.com/css?family=Poppins:400,700&display=swap);
body {
  background: #2d2d2d;
  margin: 0;
  font-family: "Poppins", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  justify-content: center;
}

.main {
  visibility: hidden;
  display: flex;
  width: 1200px;
  justify-content: center;
  align-items: center;
  height: 100vh;
  position: relative; }
  .main .title-container {
    position: absolute;
    left: -100px;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg); }
    .main .title-container .title-layer {
      overflow: hidden;
      border-bottom: 2px solid #646464;
      padding: 2px 11px; }
      .main .title-container .title-layer .title {
        color: #c6c6c6;
        margin: 0;
        font-size: 16px;
        font-weight: 600;
        letter-spacing: 5px; }
  .main .container {
    width: 960px;
    margin-left: 100px;
    height: 100vh;
    display: flex;
    align-items: center; }
    .main .container .img-container {
      width: 100%;
      height: 100%;
      position: relative;
      overflow: hidden; }
      .main .container .img-container .img-overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        right: 0;
        background: #2d2d2d; }
      .main .container .img-container img {
        width: 960px;
        margin: 0 auto; }

