.main {
  visibility: hidden;
  display: flex;
  width: 1200px;
  justify-content: center;
  align-items: center;
  height: 100vh;
  position: relative;
  .title-container {
    position: absolute;
    left: -100px;
    transform: rotate(-90deg);
    .title-layer {
      overflow: hidden;
      border-bottom: 2px solid #646464;
      padding: 2px 11px;
      .title {
        color: #c6c6c6;
        margin: 0;
        font-size: 16px;
        font-weight: 600;
        letter-spacing: 5px;
      }
    }
  }

  .container {
    width: 960px;
    margin-left: 100px;
    height: 100vh;
    display: flex;
    align-items: center;

    .img-container {
      width: 100%;
      height: 100%;
      position: relative;
      overflow: hidden;

      .img-overlay {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        right: 0;
        background: #2d2d2d;
      }

      img {
        width: 960px;
        margin: 0 auto;
      }
    }
  }
}
